.main-block {
  background-color: $color-black-pearl;
  background-image: url("../../../img/main-block/background.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  @include retina {
    background-image: url("../../../img/main-block/background@2x.jpg");
  }

  @include vp-1023 {
    background-image: url("../../../img/main-block/background-tablet.jpg");

    @include retina {
      background-image: url("../../../img/main-block/background-tablet@2x.jpg");
    }
  }

  @include vp-767 {
    background-image: url("../../../img/main-block/background-mobile.jpg");

    @include retina {
      background-image: url("../../../img/main-block/background-mobile@2x.jpg");
    }
  }
}

.main-block__wrapper {
  min-height: 880px;
  padding: 305px 185px 200px;

  @include vp-1023 {
    min-height: 640px;
    padding-top: 180px;
    padding-bottom: 60px;
  }

  @include vp-767 {
    min-height: 560px;
    padding-top: 150px;
  }
}

.main-block__title {
  margin: 0 0 32px;

  font-family: $font-roboto-condensed;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;

  @include vp-1023 {
    margin: 0;

    font-size: 64px;
    line-height: 75px;
  }

  @include vp-767 {
    margin: 0 0 8px;

    font-size: 36px;
    line-height: 42px;
  }
}

.main-block__text {
  max-width: 685px;
  margin: 0 0 46px;

  font-size: 22px;
  line-height: 34px;

  @include vp-1023 {
    margin: 0 0 58px;
  }

  @include vp-767 {
    margin: 0 0 44px;

    font-size: 14px;
    line-height: 22px;
  }
}

.main-block__button {
  margin: 0;

  @include vp-1023 {
    margin-left: 45px;
  }

  @include vp-767 {
    margin: 0;
  }
}
